import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			loadingType: 'more',
			orderList:{},
			page:0,
			navList: [{
					state: 0,
					text: '全部'
				},
				{
					state: 1,
					text: '待付款'
				},
				{
					state: 3,
					text: '待收货'
				},
				{
					state: 5,
					text: '已收货'
				},
				{
					state: 100,
					text: '未结算佣金'
				},
				{
					state: 101,
					text: '已结算佣金'
				}
			],
		};
	},
	onShow() {
		
	},
	onLoad(options){
		if(options.state == undefined){
			options.state = 0;
		}
		this.loadData();
	},	
	//下拉刷新
	onPullDownRefresh() {
	    this.loadData("refresh");
	},
	//加载更多
	onReachBottom() {
	    this.loadData('more');
	},
	methods: {
		//获取订单列表
		loadData(type='init', loading){
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			
			//没有更多直接返回
			if(type === 'refresh' || type == 'init'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more';
			}
			if(type === 'refresh' || type == 'init' || type=='tabChange'){
				this.orderList = [];
				this.page = 0;
			}
			navItem.loadingType = 'loading';
			
			setTimeout(()=>{
				this.page = this.page + 1;
				uni.request({
					url: this.$Url + '/clmsj/order/getShopOrderList', // http://bull.hnw.net/index/bull/client_wash_info
					data: {
						page:this.page,
						state: state
					},
					header:{ 'auth' : uni.getStorageSync('session_key') },
					method:'POST',
					success: (res) => {
						uni.hideLoading();
						if (res.data.code == 0) {
							let orderList = res.data.data;
							orderList.forEach(item=>{
								item = Object.assign(item, this.orderStateExp(item.state));
							})
							
							this.orderList = this.orderList.concat(orderList);
							console.log(this.orderList)
						}
					},
				})
				//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
				this.$set(navItem, 'loaded', true);
				
				//判断是否还有数据， 有改为 more， 没有改为noMore 
				navItem.loadingType = 'more';
			}, 600);	
		}, 

		//swiper 切换
		changeTab(e){
			this.tabCurrentIndex = e.target.current;
			this.loadData('refresh');
		},
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
		},
		viewExpress(item){
			console.log('viewExpress')
			uni.navigateToMiniProgram({
				appId: 'wx6885acbedba59c14',
				path: 'pages/result/result?nu=73127970405168&com=&querysource=third_xcx',
				success(res) {
					// 打开成功
				}
			})
		},
		orderDetail(item){
			console.log('order detail')
			uni.navigateTo({
				url:'/pages/shop/detail?id=' + item.id
			})
		},
		
		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case 0:
					stateTip = '待付款'; break;
				case 1:
					stateTip = '待付款'; break;
				case 2:
					stateTip = '待发货'; break;
				case 3:
					stateTip = '已发货，待收货'; break;
				case 4:
					stateTip = '已收货'; break;
				case 9:
					stateTip = '订单已关闭'; 
					stateTipColor = '#909399';
					break;
					
				//更多自定义
			}
			return {stateTip, stateTipColor};
		}
	},
}
